import styled from '@emotion/styled';
import { useRef, useState } from 'react';
import { useEffect } from 'react';
import { useNavigate, useParams } from 'react-router';
import { useSelector, useDispatch } from 'react-redux';
import AuthHttp from '../../http/authHttp';
import Layout from '../../components/layout/Layout';
import MypageNav from '../../components/mypage/myPageNav';
import {
  Wrap,
  Text,
  BoxWrap,
  PageWrap,
  RedIconWrap,
  RedIcon,
  IconText,
} from './MyPosts';
import { AccountWrap, ProfileRound, Button } from './MyAccount';

const authHttp = new AuthHttp();

const ChangeUserinfo = () => {
  const params = useParams();
  const { userId } = params;
  const previewRef = useRef();

  const navigate = useNavigate();

  const username = useSelector(
    state => state.persistedReducer.userReducer.username
  );

  const isSocialLogin = useSelector(
    state => state.persistedReducer.userReducer.isSocialLogin
  );

  // state 
  const [UserInfo, setUserInfo] = useState([]);

  //    ui 
  const [isChangePassword, setIsChangePassword] = useState(false);

  //  
  const [Preview, setPreview] = useState('');

  //   
  const [Profile, setProfile] = useState('');

  useEffect(() => {
    onMypage();
  }, []);

  const onMypage = async () => {
    try {
      const res = await authHttp.getMypage(userId);
      setUserInfo(res.data.result);
      console.log(res);
    } catch (err) {
      console.log(err);
    }
  };

  //   
  const savePreview = () => {
    if (!previewRef.current || !previewRef.current.files) {
      return;
    }

    //Preview sate 
    const file = previewRef.current.files[0];
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      setPreview(reader.result);
    };

    //  formdata Profile state 
    const formData = new FormData();
    formData.append('image', previewRef.current.files[0]);
    setProfile(formData);
    // setProfile(previewRef.current.files[0]);
  };

  //    
  const sendProfile = async () => {
    try {
      const res = await authHttp.postProfile(userId, Profile);
      console.log(res);
    } catch (err) {
      console.log(err);
    }
  };

  const onSubmit = e => {
    e.preventDefault();
    console.log('preview', Preview);
    console.log('profile', Profile);

    if (Preview) {
      sendProfile();
    }

    navigate(`/mypage/changeuserinfo/${userId}`);
  };

  return (
    <Layout>
      <Wrap>
        <Text></Text>
        {username === UserInfo.username ? (
          <>
            <BoxWrap>
              <MypageNav
                userNickName={UserInfo.nickname}
                userName={UserInfo.username}
                userEmail={UserInfo.email}
                categoryName='account'
                userId={userId}
              />
              <PageWrap>
                <RedIconWrap>
                  <RedIcon>
                    <img src='/image/mypage-account-r.png' alt='icon' />
                  </RedIcon>
                  <IconText></IconText>
                </RedIconWrap>
                {isSocialLogin ? (
                  <AccountWrap>
                    <ProfileRound>
                      <img />
                    </ProfileRound>
                    <ProfileButton
                      placeholder=' '
                      type='file'
                      accept='image/*'
                    />
                    <InputTitle></InputTitle>
                    <InputWrap marginBottom>
                      <InfoInput />
                      <CheckButton> </CheckButton>
                    </InputWrap>
                  </AccountWrap>
                ) : (
                  <AccountWrap>
                    <ProfileRound>
                      {Preview ? (
                        <ProfileImg src={Preview} alt=' ' />
                      ) : (
                        ''
                      )}
                    </ProfileRound>
                    <ProfileButton htmlFor='profileImg'>
                       
                    </ProfileButton>
                    <ProfileInput
                      type='file'
                      accept='image/*'
                      id='profileImg'
                      onChange={savePreview}
                      ref={previewRef}
                    />
                    <InputTitle></InputTitle>
                    <InputWrap marginBottom>
                      <InfoInput />
                      <CheckButton> </CheckButton>
                    </InputWrap>
                    <InputTitle></InputTitle>
                    <InputWrap marginBottom>
                      <InfoInput />
                      <CheckButton> </CheckButton>
                    </InputWrap>
                    <InputTitle></InputTitle>
                    <InputWrap marginBottom={isChangePassword}>
                      <InfoInput />
                      <CheckButton> </CheckButton>
                    </InputWrap>
                    {isChangePassword ? (
                      <>
                        <InputTitle> </InputTitle>
                        <InputWrap marginBottom>
                          <InfoInput />
                          <CheckButton> </CheckButton>
                        </InputWrap>

                        <InputTitle> </InputTitle>
                        <InputWrap marginBottom>
                          <InfoInput />
                          <CheckButton> </CheckButton>
                        </InputWrap>
                        <InputTitle>  </InputTitle>
                        <InputWrap marginBottom>
                          <InfoInput />
                          <CheckButton> </CheckButton>
                        </InputWrap>
                      </>
                    ) : (
                      <Button button onClick={() => setIsChangePassword(true)}>
                         
                      </Button>
                    )}
                  </AccountWrap>
                )}
                <SubmitButton onClick={e => onSubmit(e)}></SubmitButton>
              </PageWrap>
            </BoxWrap>
          </>
        ) : (
          navigate('/notfound')
        )}
      </Wrap>
    </Layout>
  );
};

const ProfileButton = styled.label`
  width: 130px;
  height: 50px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 600;
  border: 1px solid #ff4122;
  color: #ff4122;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s;
  cursor: pointer;
  margin-bottom: 50px;
  box-sizing: border-box;

  &:hover {
    background-color: #ff4122;
    color: white;
  }

  @media screen and (max-width: 1700px) {
    height: 35px;
    margin-bottom: 20px;
    font-size: 14px;
  }
`;

const ProfileInput = styled.input`
  display: none;
`;

const ProfileImg = styled.img`
  height: 100%;
  width: auto;
`;

const InputTitle = styled.div`
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 10px;
  width: 343px;
  text-align: left;

  @media screen and (max-width: 1700px) {
    width: 320px;
    font-size: 14px;
    margin-bottom: 7px;
    font-weight: 600;
  }
`;

const InputWrap = styled.div`
  display: flex;
  margin-bottom: ${props => (props.marginBottom ? '10px' : '30px')};
  width: 343px;
  justify-content: space-between;

  @media screen and (max-width: 1700px) {
    width: 320px;
    margin-bottom: ${props => (props.marginBottom ? '7px' : '20px')};
  }
`;

const InfoInput = styled.input`
  width: 220px;
  height: 50px;
  padding: 5px 15px;
  box-sizing: border-box;
  font-size: 16px;
  color: #000000;
  border: 1px solid #a9a9af;
  border-radius: 5px;

  ::placeholder {
    font-size: 16px;
    color: #000000;
  }

  @media screen and (max-width: 1700px) {
    width: 210px;
    height: 35px;
    font-size: 14px;

    ::placeholder {
      font-size: 14px;
    }
  }
`;

const CheckButton = styled.div`
  width: 100px;
  height: 50px;
  color: #dcdcdc;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #dcdcdc;
  border-radius: 5px;

  @media screen and (max-width: 1700px) {
    width: 95px;
    height: 35px;
    font-size: 14x;
  }
`;

const SubmitButton = styled.div`
  position: absolute;
  width: 120px;
  height: 50px;
  border-radius: 5px;
  background-color: #ff4122;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  top: 3%;
  left: 86%;
  color: white;
  font-weight: 600;
  font-size: 16px;

  @media screen and (max-width: 1700px) {
    width: 110px;
    height: 35px;
    font-size: 14px;
  }
`;

export default ChangeUserinfo;
